body {
  margin: 0px; }

#demo {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: none; }
  #demo #status {
    display: inline-block;
    width: 300px;
    margin: 20px;
    padding: 20px;
    box-shadow: 0px 0px 5px 0px #5f5f5f; }
    #demo #status span {
      float: right; }
    #demo #status .direction span {
      display: inline-block; }
    #demo #status .title {
      margin-bottom: 20px;
      text-align: center;
      font-size: 18px; }
  #demo #mouse {
    position: absolute;
    width: 0px;
    height: 0px;
    margin-top: -5px;
    margin-left: -5px;
    border: 5px solid;
    border-radius: 50%; }
  #demo #mouse span {
    display: inline-block;
    position: absolute;
    transform-origin: left; }
